<template>
  <div class="renzheng">
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>1、扫描需要识别的二维码</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="cert-content" @click="goScan()">
            <div class="cert-icon"><img src="@/assets/images/zj/icon-saoma2.gif"></div>
          </div>
        </div>
      </div>

      <div v-if="!needIdCard " class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>2、请拍摄身份证正面</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="cert-content" @click="goPhone(1001)">
            <div v-if="!IDCards1" class="cert-icon"><img src="@/assets/images/zj/sfzzm.png"></div>
            <!-- 结果展示 -->
            <div class="cert-result">
              <div v-if="IDCards1" class="result-icon"><img :src="img1" style="width:300px; height:200px;"></div>
              <div v-if="IDCards1" class="result-text"><img src="@/assets/images/zj/icon-rzcg.png">身份证正面识别成功</div>
            </div>
          </div>
        </div>
      </div>

      <div v-if="!needIdCard" class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>3、请拍摄身份证反面</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="cert-content" @click="goPhone(2001)">
            <div v-if="!IDCards2" class="cert-icon"><img src="@/assets/images/zj/sfzfm.png"></div>
            <!-- 结果展示 -->
            <div class="cert-result">
              <div v-if="IDCards2" class="result-icon"><img :src="img2" style="width:300px; height:200px;"></div>
              <div v-if="IDCards2" class="result-text"><img src="@/assets/images/zj/icon-rzcg.png">身份证反面识别成功</div>
            </div>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div v-if="!needIdCard" class="title-left">
              <h2>4、请完成脸部识别</h2>
            </div>
            <div v-else class="title-left">
              <h2>2、请完成脸部识别</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="cert-content" @click="goFace(3001)">
            <div v-if="!IDCards3" class="cert-icon"><img src="@/assets/images/zj/icon-rlsb.png"></div>
            <div class="cert-result">
              <div v-if="IDCards3" class="result-icon"><img :src="faceImage" style="width:200px; height:300px;"></div>
              <div v-if="IDCards3" class="result-text"><img src="@/assets/images/zj/icon-rzcg.png">脸部识别成功</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter, NativeJS } from '@/common/js/mixin.js'
// import { compressImage } from '@/common/js/tpys' // 引入图片压缩js
export default {
  name: 'ZjSfrz',
  components: {
    headerBar
  },
  mixins: [pageRouter, NativeJS],
  data() {
    return {
      serverId: '',
      localIds: [],
      file1: '',
      file2: '',
      file3: '',
      token: '',
      img2: '',
      img1: '',
      zjmessage: '',
      isScan: false,
      needIdCard: false,
      IDCards1: false,
      IDCards2: false,
      IDCards3: false,
      faceImage: '',
      isBack: '',
      baseurl: '',
      headerData: {
        title: '身份认证',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  watch: {
    // 二维码数据
    Native_ScanData(val) {
      console.info('Native_ScanData===0==', val, val.name)
      if (val) {
        console.info('val===1==', val.name)
        // alert(val.name)
        this.getScanData(val.name)
        this.token = val.name
      }
    },
    // 照相机、图片返回数据
    Native_PhoneImageData(val) {
      console.info('Native_ScanData===0==', val, val.images)
      if (val) {
        if (val.name === 'IDForn') {
          // this.file1 = val.images
          this.getPhoneImage(1001, val.images)
        } else if (val.name === 'IDBack') {
          this.getPhoneImage(2001, val.images)
          // this.file2 = val.images
        } else if (val.name === 'face') {
          // this.file3 = val.images
          this.getFaceImage(val.images)
        }
      }
    }
  },
  methods: {
    goBackPage() {
      // this.$router.go(-1)
      this.$router.push('zjhome')
    },
    dissDialog() {
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.dissDialog()
        } catch (err) {
          console.log(err)
        }
        try {
          window.webkit.messageHandlers.dissDialog.postMessage()
        } catch (err) {
          console.log(err)
        }
      } else {
        this.$toast('返回原生页面')
      }
    },
    // 二维码扫描
    goScan() {
      //  this.getScanData('OThhYTVmOGFiZmM4NDY4N2E1NDA3MjEwMzlmMDFjYzc.1612227839737.61c82dfb585cdbd79f59ed352fb80c61')
      this.NativeScan()

      // sdk.getJSSDK('')
      // wx.scanQRCode({
      //   needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
      //   scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
      //   success: function(res) {
      //     var result = res.resultStr // 当needResult 为 1 时，扫码返回的结果
      //     console.log('result=========', result)
      //     that.getScanData('MmJjODZjMDYyOWIzNGRiN2FhYWU4MWUzZGU3YmJhNTg.1594358294077.bc6d4ca97e71cd1d26f4239485350c6c')
      //     that.token = 'MmJjODZjMDYyOWIzNGRiN2FhYWU4MWUzZGU3YmJhNTg.1594358294077.bc6d4ca97e71cd1d26f4239485350c6c'
      //   }
      // })
    },
    getScanData(token) {
      this.$store.dispatch('zj/getSfxxAction', token).then(res => {
        res = JSON.parse(res)
        this.zjmessage = res
        if (res !== null) {
          this.isScan = true
        } else {
          this.$toast.fail('二维码信息错误，请核对二维码')
          return
        }
        if (res.method === 4) {
          this.getCertStatus(token)
          this.baseurl = res.url
        } else {
          this.$toast.fail('二维码错误！')
          return
        }
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },
    getCertStatus(token) {
      const param = new FormData() // 创建form对象
      param.append('idcard', this.zjmessage.idcard)
      param.append('name', this.zjmessage.name)
      console.info('this.zjmessage======', this.zjmessage.idcard)
      console.info('this.zjmessage======', this.zjmessage.name)
      console.info('this.baseurl======', this.zjmessage.url)

      this.$store.dispatch('zj/getCertStatusAction', {
        data: param,
        baseurl: this.zjmessage.url,
        token: token
      }).then(res => {
        if (res.text === '未找到验证记录') {
          this.needIdCard = false
        }
        if (res.text === '存在已验证记录') {
          this.needIdCard = true
          this.IDCards1 = true
          this.IDCards2 = true
        }
        this.$toast.success('扫描成功')
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },
    // 身份验证
    goPhone(type) {
      if (!this.isScan) {
        this.$toast('请先进行扫码')
        return
      }
      this.NativePhone(type)
      // wx.chooseImage({
      //   count: 1, // 默认9
      //   sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
      //   sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
      //   success: function(res) {
      //     that.localIds = res.localIds // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
      //     console.log('lovalIds======', that.localIds)
      //     wx.getLocalImgData({
      //       localId: that.localIds[0], // 图片的localID
      //       success: function(res) {
      //         var localData = res.localData // localData是图片的base64数据，可以用img标签显示
      //         if (that.is_phone()) {
      //           // 如果是安卓系统需要对wx.getLocalImgData获得的base64进行处理
      //           localData = 'data:image/jpeg;base64,' + localData.replace(/\n/g, '')
      //         }
      //         // 图片压缩
      //         console.log('压缩前====', localData)
      //         compressImage(localData, {
      //           width: 100, // 压缩后图片的宽
      //           height: 200, // 压缩后图片的高，宽高若只传一个，则按图片原比例进行压缩
      //           quality: 0.8 // 压缩后图片的清晰度，取值0-1，不传默认为0.7，值越小，所绘制出的图像越模糊
      //         }).then(res => {
      //           console.log('压缩后====', res)
      //           that.getPhoneImage(type, res) // result即为压缩后的结果
      //         })
      //       }
      //     })
      //   }
      // })
    },

    is_phone() {
      var u = navigator.userAgent
      var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
      var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
      if (isAndroid) {
        // 这个是安卓操作系统
        return true
      }
      if (isIOS) {
        // 这个是ios操作系统
        return false
      }
    },

    getPhoneImage(type, files) {
      let url = ''
      if (type === 1001) {
        url = 'zj/updateCardFrontAction'
      } else {
        if (!this.IDCards1) {
          this.$toast('请先完成身份证正面认证')
          return
        }
        url = 'zj/updateCardBackAction'
      }
      console.log('url===========', url)
      const param = new FormData() // 创建form对象
      param.append('file', this.dataURLtoFile(files, 'i.jpg'), 'head_image')// 通过append向form对象添加数据
      param.append('idcard', this.zjmessage.idcard)
      param.append('name', this.zjmessage.name)
      this.$store.dispatch(url, {
        token: this.token,
        data: param,
        baseurl: this.baseurl
      }).then(res => {
        console.log('res=====', res)
        if (type === 1001) {
          this.IDCards1 = true
          this.img1 = files
          this.$toast.success('身份证正面认证成功！')
        } else {
          this.IDCards2 = true
          this.img2 = files
          this.$toast.success('身份证反面认证成功！')
        }
      }).catch(error => {
        this.$toast(error)

        console.log('error=====', error)
      })
    },
    dataURLtoFile(dataurl, filename) {
      var arr = dataurl.split(',')
      var mime = arr[0].match(/:(.*?);/)[1]
      var bstr = atob(arr[1])
      var n = bstr.length
      var u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new File([u8arr], filename, { type: mime })
    },
    // 人脸识别
    goFace() {
      if (!this.IDCards1 && !this.IDCards2) {
        this.$toast('请先完成身份证认证')
        return
      }
      if (!this.IDCards1) {
        this.$toast('请先完成身份证正面认证')
        return
      }
      if (!this.IDCards2) {
        this.$toast('请先完成身份证反面认证')
        return
      }
      this.NativePhone(3001)
      console.log('===========================')
      // wx.chooseImage({
      //   count: 1, // 默认9
      //   sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
      //   sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
      //   success: function(res) {
      //     _this.localIds = res.localIds // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
      //     console.log('lovalIds======', _this.localIds)
      //     wx.getLocalImgData({
      //       localId: _this.localIds[0], // 图片的localID
      //       success: function(res) {
      //         var localData = res.localData // localData是图片的base64数据，可以用img标签显示
      //         if (_this.is_phone()) {
      //           // 如果是安卓系统需要对wx.getLocalImgData获得的base64进行处理
      //           localData = 'data:image/jpeg;base64,' + localData.replace(/\n/g, '')
      //         }
      //         _this.getFaceImage(localData)
      //       }
      //     })
      //   }
      // })
    },
    getFaceImage(files) {
      const param = new FormData() // 创建form对象
      param.append('file', this.dataURLtoFile(files, 'i.png'), 'head_image')// 通过append向form对象添加数据      param.append('idcard', this.zjmessage.idcard)
      param.append('name', this.zjmessage.name)
      this.$store.dispatch('zj/matchIdcardPhotoAction', {
        token: this.token,
        data: param,
        baseurl: this.zjmessage.url
      }).then(res => {
        this.IDCards3 = true
        this.faceImage = files
        this.$toast.success('身份认证成功！')
        console.log('res=====', res)
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    }
  }
}
</script>

<style lang="css" scoped>
.cert-content{margin:30px 0}
.cert-content .cert-icon{text-align: center;max-width: 120px;margin: 0 auto;margin-top:10px; margin-bottom: 10px;}
.cert-content .cert-icon > img,.cert-result .result-icon > img{width: 100%;height: auto;}
.cert-result .result-icon{width:95%;text-align: center;margin: 0 auto;margin-top:10px; margin-bottom: 10px;}
.result-text{font-size: 14px; color: #666F83;display: flex;align-items: center;justify-content: center;margin:10px 0;}
.result-text > img{width: 14px; height: 14px;margin:0 10px 0 0;}
</style>
